@media (min-width: 640px) and (max-width: 1023px) {
  .menu {
    grid-template-columns: repeat(6, 68px);
    column-gap: 30px;
  }
}

@media (min-width: 320px) and (max-width: 639px) {
  .menu {
    grid-template-columns: repeat(2, 130px);
    column-gap: 20px;
  }
}

.menu:target {
  position: fixed;
  transform: translateX(0);
  overflow: hidden;
}

.menu__navigation {
  align-self: start;
  grid-row: 1/2;
  grid-column: 1/-1;
}

.menu__redirect {
  margin-bottom: 16px;
  grid-column: 1/-1;
}

.redirect {
  padding-top: 48px;
}

.redirect__item {
  margin-bottom: 24px;
}

.redirect__item--last {
  padding-top: 16px;
}

.redirect__link {
  display: inline-block;
  border-bottom: 2px solid transparent;
  padding-bottom: 8px;
  color: #fff;
  font-family: Poppins, sans-serif;
  font-size: 22px;
  font-style: normal;
  font-weight: 700;
  line-height: 150%;
  letter-spacing: 3px;
  text-transform: uppercase;
  text-decoration: none;
  transition: all 0.5s ease-in-out;
}

.redirect__link:hover {
  color: #d5d7de;
  border-bottom: 2px solid #d5d7de;
  cursor: url(./images/button/pointer.svg), pointer;
}

